* {
    margin: 0;
    padding: 0;
    font-family: 'Lexend Peta', sans-serif;
    box-sizing: border-box;
}

.contact {
    position: relative;
    min-height: 100vh;
    padding: 50px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: url(../../public/media/bg2.jpg);
    background-size: cover;
    background-position: center;
}

.contact::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
     background: rgba(0, 0, 0, 0.5); /*Dark overlay for better contrast */
    z-index: 1;
}

.contact .content {
    max-width: 800px;
    text-align: center;
    position: relative;
    z-index: 2;
}

.contact .content h2 {
    font-size: 36px;
    font-weight: 500;
    color: #fff; /* Changed to white for better contrast */
    margin-bottom: 20px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8); /* Added shadow for readability */
}

.contact .content p {
    font-weight: 300;
    color: #dcdcdc; /* Light grey color */
}

.container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    gap: 20px;
}

.container .contactInfo,
.container .contactForm {
    width: 45%; /* Reduced the width of both columns */
}
.container .contactInfo {
    width: 50%; /* لضمان التكيف مع المساحة */
    padding: 40px; /* إضافة مساحة داخلية */
    border-radius: 8px; /* إضافة زوايا مستديرة */
    z-index: 2;
    color: #fff; /* لون النص أبيض ليكون واضحًا */
}

.container .contactInfo .box {
    position: relative;
    padding: 20px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ddd; /* Added border to separate boxes */
    margin-bottom: 20px;
}

.container .contactInfo .box .icon {
    min-width: 70px;
    height: 70px;
    background: #fff;
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 28px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3)
}

.container .contactInfo .box .text h3 {
    margin-left: 20px;
    font-size: 18px;
    color:  #ffffff; /* Changed to a more vibrant color */
    font-weight: 600;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
}

.container .contactInfo .box .text p {
    margin-left: 20px;
    font-size: 16px;
    color: #e0e0e0;
    font-weight: 400;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
}

.contactForm {
    width: 100%; /* Ensured full width on small screens */
    padding: 40px;
    background: #fff;
    border-radius: 8px; /* Added rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Added subtle shadow */
    z-index: 2;
}

.contactForm h2 {
    font-size: 30px;
    color: #333;
    font-weight: 500;
    margin-bottom: 20px;
}

.contactForm .inputBox {
    position: relative;
    width: 100%;
    margin-top: 10px;
}

.contactForm .inputBox input,
.contactForm .inputBox textarea {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    margin: 10px 0;
    border: 2px solid #ddd; /* Changed border to grey */
    border-radius: 4px; /* Rounded corners */
    outline: none;
    resize: none;
}

.contactForm .inputBox span {
    position: absolute;
    left: 0;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    pointer-events: none;
    transition: 0.5s;
    color: #666;
}

.contactForm .inputBox input:focus ~ span,
.contactForm .inputBox input:valid ~ span,
.contactForm .inputBox textarea:focus ~ span,
.contactForm .inputBox textarea:valid ~ span {
    color: #ff0000;
    font-size: 12px;
    transform: translateY(-20px);
}

.contactForm .inputBox input[type="submit"] {
    width: 100%;
    background: #00bcd4;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
}

.contactForm .inputBox input[type="submit"]:hover {
    background: #0097a7; /* Darker blue on hover */
}

/* Responsive Styles */
@media (max-width: 768px) {
    .container {
        flex-direction: column;
        padding: 10px;
    }
    .contact-item {
        margin-bottom: 15px;
    }

    .container .contactInfo,
    .container .contactForm {
        width: 180%; /* Full width on mobile */
    }
}
